"use client";
import { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { loginAction, setDemoModeAction, requestLoginCode, requestRegistration, seedDatabase } from '@/app/actions';
import { useRouter } from 'next/navigation';
import { Lock, Eye, Mail, ArrowRight, UserPlus, Users, Info } from 'lucide-react';
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";

export default function LoginPage() {
  const router = useRouter();
  const [email, setEmail] = useState('');
  const [token, setToken] = useState('');
  const [familyName, setFamilyName] = useState('');
  
  const [step, setStep] = useState(1);
  const [loading, setLoading] = useState(false);

  // Auto-inicializar BD la primera vez (Hack seguro)
  useEffect(() => {
      // Intentamos correr el seed silenciosamente por si la BD está vacía
      seedDatabase().catch(() => {});
  }, []);

  const handleRequestCode = async (e: React.FormEvent) => {
      e.preventDefault();
      setLoading(true);
      const res = await requestLoginCode(email);
      setLoading(false);
      
      if (res.success) {
          setStep(2);
      } else {
          alert(res.message);
      }
  };

  const handleLogin = async (e: React.FormEvent) => {
      e.preventDefault();
      setLoading(true);
      const res = await loginAction(email, token);
      if (res.success) {
          // El router.push a veces es lento en refrescar cookies, force reload es más seguro para auth
          window.location.href = '/'; 
      } else {
          alert("Código incorrecto");
          setLoading(false);
      }
  };

  const handleRegister = async (e: React.FormEvent) => {
      e.preventDefault();
      setLoading(true);
      const res = await requestRegistration(email, familyName);
      setLoading(false);
      if (res.success) alert("✅ Solicitud enviada al administrador.");
      else alert("Error: " + res.message);
  };

  const handleDemo = async () => {
      setLoading(true);
      await setDemoModeAction();
      window.location.href = '/';
  };

  return (
    <div className="min-h-screen bg-black flex items-center justify-center p-4">
        <div className="w-full max-w-md bg-zinc-900 border border-zinc-800 p-8 rounded-2xl shadow-2xl">
            <div className="text-center mb-6">
                <div className="inline-flex items-center justify-center w-12 h-12 rounded-full bg-indigo-900/30 text-indigo-400 mb-4">
                    <Lock className="w-6 h-6" />
                </div>
                <h1 className="text-2xl font-bold text-white">Bienvenido</h1>
                <p className="text-zinc-500 text-sm">Gestión Financiera Familiar</p>
            </div>

            <Tabs defaultValue="login" className="w-full">
                <TabsList className="grid w-full grid-cols-2 bg-zinc-950 mb-6">
                    <TabsTrigger value="login">Ingresar</TabsTrigger>
                    <TabsTrigger value="register">Solicitar</TabsTrigger>
                </TabsList>

                <TabsContent value="login">
                    {step === 1 ? (
                        <form onSubmit={handleRequestCode} className="space-y-4">
                            <div className="space-y-2">
                                <label className="text-xs text-zinc-400 ml-1">Correo Electrónico</label>
                                <Input 
                                    type="email" placeholder="ejemplo@gmail.com" 
                                    className="bg-black border-zinc-800 h-12 text-white"
                                    value={email} onChange={e => setEmail(e.target.value)} required
                                />
                            </div>
                            <Button type="submit" disabled={loading} className="w-full h-12 bg-indigo-600 hover:bg-indigo-700 font-bold">
                                {loading ? 'Enviando...' : 'Siguiente'} <ArrowRight className="ml-2 h-4 w-4" />
                            </Button>
                        </form>
                    ) : (
                        <form onSubmit={handleLogin} className="space-y-4">
                            <div className="text-center text-zinc-400 text-sm mb-2">
                                Ingresa el código enviado a <span className="text-white">{email}</span>
                            </div>
                            <Input 
                                type="text" placeholder="TOKEN" autoFocus
                                className="bg-black border-zinc-800 h-14 text-center text-2xl tracking-widest text-emerald-400 font-mono"
                                value={token} onChange={e => setToken(e.target.value)} required
                            />
                            <Button type="submit" disabled={loading} className="w-full h-12 bg-emerald-600 hover:bg-emerald-700 font-bold">
                                {loading ? 'Validando...' : 'Entrar'}
                            </Button>
                            <button type="button" onClick={()=>setStep(1)} className="text-xs text-indigo-400 w-full mt-2 hover:underline">Corregir correo</button>
                        </form>
                    )}
                </TabsContent>

                <TabsContent value="register">
                    <form onSubmit={handleRegister} className="space-y-4">
                        <Input className="bg-black border-zinc-800" type="email" placeholder="Tu correo" required value={email} onChange={e=>setEmail(e.target.value)} />
                        
                        <div className="space-y-1">
                            <Input className="bg-black border-zinc-800" placeholder="Nombre Grupo Familiar" required value={familyName} onChange={e=>setFamilyName(e.target.value)} />
                            <p className="text-[10px] text-zinc-500 flex items-center gap-1 ml-1">
                                <Info className="h-3 w-3" /> Recuerda: Debe ser el mismo nombre que tu pareja.
                            </p>
                        </div>

                        <Button type="submit" disabled={loading} className="w-full bg-zinc-800 hover:bg-zinc-700 border border-zinc-700">
                             <UserPlus className="mr-2 h-4 w-4" /> Solicitar Acceso
                        </Button>
                    </form>
                </TabsContent>
            </Tabs>

            <div className="mt-8 pt-6 border-t border-zinc-800">
                <Button variant="outline" onClick={handleDemo} className="w-full border-zinc-700 hover:bg-zinc-800 text-zinc-300">
                    <Eye className="mr-2 h-4 w-4" /> Ver Demo (Datos Falsos)
                </Button>
            </div>
        </div>
    </div>
  );
}